.gallery {
  padding: 0 20px;

  &__title {
    margin: 0;

    @include firstText;
    font-weight: bold;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    margin-bottom: 40px;
    color: $colorTitleAndFooter;
  }

  &__img {
    display: block;
    width: 100%;
    margin-bottom: 100px;
  }
}
